import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import { ListSyaratPage } from './list_syarat';

/**
 * Generated class for the SyaratLayananPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-syarat-layanan',
  templateUrl: 'syarat-layanan.html',
})
export class SyaratLayananPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SyaratLayananPage');
  }

  detail()
  {
    this.modalCtrl.create(ListSyaratPage).present();
  }

}



